/*===== GOOGLE FONTS =====*/
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap");

/*===== VARIABLES CSS =====*/
:root {
	--header-height: 3rem;
	--font-medium: 500;
}

/*===== Colors =====*/
:root {
	--first-color: #5361ff;
	--white-color: #FAFAFF;
	--dark-color: #2A3B47;
	--text-color: #697477;
}

:root {
	--body-font: 'Montserrat', sans-serif;
	--big-font-size: 6.25rem;
	--h2-font-size: 1.25rem;
	--normal-font-size: .938rem;
	--small-font-size: .813rem;
}

/*===== Margins =====*/
:root {
	--mb-1: .5rem;
	--mb-2: 1rem;
	--mb-3: 1.5rem;
	--mb-4: 2rem;
}

/*===== z index =====*/
:root {
	--z-fixed: 100;
}

/*===== BASE =====*/
*, ::before, ::after {
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

body {
	margin: var(--header-height) 0 0 0;
	font-family: var(--body-font);
	font-size: var(--normal-font-size);
	font-weight: var(--font-medium);
	color: var(--text-color);
	line-height: 1.6;
}

h1, h2, p {
	margin: 0;
}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

a {
	text-decoration: none;
	color: var(--text-color);
}

img {
	max-width: 100%;
	height: auto;
	display: block;
}

.section {
	padding: 3rem 0;
}

.section-title {
	position: relative;
	font-size: var(--h2-font-size);
	color: var(--dark-color);
	margin: var(--mb-4) 0;
	text-align: center;
}

.section-title::after {
	position: absolute;
	content: "";
	width: 32px;
	height: .18rem;
	left: 0;
	right: 0;
	margin: auto;
	top: 2rem;
	background-color: var(--first-color);
}

.bg-grid {
	display: grid;
	grid-template-columns: 100%;
	grid-column-gap: 2rem;
	width: calc(100% - 2rem);
	margin-left: var(--mb-2);
	margin-right: var(--mb-2);
}

.l-header {
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: var(--z-fixed);
	background-color: var(--first-color);
}

.nav {
	height: var(--header-height);
	display: flex;
	justify-content: space-between;
	align-items: center;
}

@media screen and (max-width: 768px) {
	.nav__menu {
		position: fixed;
		top: var(--header-height);
		right: -100%;
		width: 80%;
		height: 100%;
		padding: 2rem;
		background-color: rgba(255, 255, 255, .3);
		transition: .5s;
		backdrop-filter: blur(10px);
	}
}

.nav__item {
	margin-bottom: var(--mb-4);
}

.nav__link {
	position: relative;
	color: var(--dark-color);
}

.nav__link:hover {
	color: var(--first-color);
}

.nav__logo {
	color: var(--white-color);
}

.nav__toggle {
	color: var(--white-color);
	font-size: 1.5rem;
	cursor: pointer;
}

.show {
	right: 0;
}

.active::after {
	position: absolute;
	content: "";
	width: 100%;
	height: .18rem;
	left: 0;
	top: 2rem;
	background-color: var(--first-color);
}

.home {
	position: relative;
	background-color: var(--first-color);
	overflow: hidden;
}

.home__container {
	height: calc(100vh - var(--header-height));
	row-gap: 5rem;
}

.home__title {
	align-self: flex-end;
	font-size: var(--big-font-size);
	color: var(--white-color);
	line-height: .8;
}

.home__title span {
	text-shadow: 0 20px 25px rgba(0, 0, 0, .5);
}

.home__scroll {
	align-self: flex-end;
	padding-bottom: var(--mb-4);
}

.home__scroll-link {
	writing-mode: vertical-lr;
	transform: rotate(-180deg);
	color: var(--white-color)
}

.home__img {
	position: absolute;
	right: 0;
	bottom: 0;
	width: 295px;
}

.about__container {
	justify-items: center;
	row-gap: 2rem;
	text-align: center;
}

.about__img {
	display: flex;
	justify-content: center;
	padding-top: 1rem;
	width: 120px;
	height: 120px;
	background-color: var(--first-color);
	border-radius: 50%;
	overflow: hidden;
}

.about__subtitle {
	font-size: var(--h2-font-size);
	color: var(--first-color);
	margin-bottom: var(--mb-1);
}

.about__text {
	margin-bottom: var(--mb-4);
}

.about__profession {
	display: block;
	margin-bottom: var(--mb-4);
}

.about__social-icon {
	font-size: 1.8rem;
	margin: 0 var(--mb-1);
}

.about__social-icon:hover {
	color: var(--first-color);
}

.skills__container {
	row-gap: 2rem;
}

.skills__subtitle {
	color: var(--first-color);
	margin-bottom: var(--mb-3);
}

.skills__name {
	display: inline-block;
	font-size: var(--small-font-size);
	margin-right: var(--mb-2);
	margin-bottom: var(--mb-3);
	padding: .25rem .5rem;
	background-color: var(--white-color);
	border-radius: .25rem;
}

.skills__name:hover {
	background-color: var(--first-color);
	color: var(--white-color);
}

.skills__img img {
	border-radius: .5rem;
}

.portfolio {
	background-color: var(--white-color);
}

.portfolio__container {
	justify-items: center;
	row-gap: 2rem;
}

.portfolio__box-name {
	color: var(--first-color);
	font-size: calc( (100vw - 480px)/(1280 - 480) * (24 - 20) + 18px)
}

.portfolio__box-description {
	display: flex;
	font-size: 15px;
	padding: 12px 0 5px 0;
	text-align: justify;
	flex-grow: 1;
}

.portfolio__box {
	display: flex;
	width: 100%;
	flex-direction: column;
	box-shadow: 0 5px 15px rgba(83, 97, 255, .08);
	border-radius: .5rem;
	padding: 15px;
	background-color: #fff;
}

.portfolio__box-bottom {
	display: flex;
	margin-top: 20px;
}

.portfolio__box-language, .portfolio__box-star,
.portfolio__box-forks {
	display: flex;
	padding-right: 10px;
	align-items: center;
}

.portfolio__box-title {
	padding-left: 5px;
	font-size: 16px;
}

.portfolio__box-icon {
	color: var(--text-color);
}

.footer {
	background-color: var(--dark-color);
}

.footer__container {
	row-gap: 2rem;
}

.footer__title {
	font-size: var(--normal-font-size);
	color: var(--white-color);
	margin-bottom: var(--mb-2);
}

.footer__link {
	padding: .25rem 0;
}

.footer__link:hover {
	color: var(--first-color);
}

.footer__social {
	font-size: 1.8rem;
	margin-right: var(--mb-1);
}

.footer__social:hover {
	color: var(--first-color);
}

@media screen and (max-width: 568px) {
	.about__box {
		width: 100%;
	}

	.home__img {
		width: 300px;
	}
}

@media screen and (max-width: 320px) {
	.home__img {
		width: 230px;
	}
}

@media screen and (min-width: 769px) {
	:root {
		--big-font-size: 10.5rem;
		--h2-font-size: 2rem;
		--normal-font-size: 1rem;
		--small-font-size: .875rem;
	}

	body {
		margin: 0;
	}

	.section {
		padding-top: 4rem;
	}

	.section-title {
		margin-bottom: 3rem;
	}

	.section-title::after {
		width: 64px;
		top: 3rem;
	}

	.nav {
		height: calc(var(--header-height) + 1rem);
	}

	.nav__list {
		display: flex;
	}

	.nav__item {
		margin-left: var(--mb-4);
		margin-bottom: 0;
	}

	.nav__toggle {
		display: none;
	}

	.nav__link {
		color: var(--white-color);
	}

	.nav__link:hover {
		color: var(--white-color);
	}

	.active::after {
		background-color: var(--white-color);
	}

	.home__container {
		height: 100vh;
		grid-template-rows: 1.7fr 1fr;
		row-gap: 0;
	}

	.home__img {
		width: 450px;
		right: 10%;
	}

	.about__box {
		width: 100%;
	}

	.about__container {
		grid-template-columns: repeat(2, 1fr);
		align-items: center;
		text-align: initial;
		padding: 4rem 0;
	}

	.about__img {
		width: 275px;
		height: 275px;
	}

	.skills__container {
		grid-template-columns: repeat(2, 1fr);
		align-items: center;
		justify-items: center;
	}

	.portfolio__container {
		grid-template-columns: repeat(3, 1fr);
		grid-template-rows: repeat(2, 1fr);
		column-gap: 2rem;
	}

	.contact__container {
		grid-template-columns: 100%;
		justify-items: center;
	}

	.contact__subtitle,
	.contact__text {
		text-align: center;
	}

	.footer__container {
		grid-template-columns: repeat(3, 1fr);
		justify-items: center;
	}
}

@media screen and (min-width: 1440px) {
	.about__img {
		width: 400px;
		height: 400px;
	}

	.about__box {
		width: calc(100% - 256px);
	}
}

@media screen and (min-width: 1024px) {
	.bd-grid {
		margin-left: auto;
		margin-right: auto;
	}

	.about__box {
		width: 512px;
	}
}

/* For tall screens on mobiles y desktop*/
@media screen and (min-height: 721px) {
	.home__container {
		height: 640px;
	}
}
